.carousel__wrapper {
  display: flex;
  flex-direction: row;
  width: 100%;
  height: 100%;
}

.content {
  display: inline-flex;
  flex-direction: row;
  width: 90%;
  height: -moz-fit-content;
  height: fit-content;
  margin: auto;
  flex-direction: row;
  justify-content: space-evenly;
}

.prev {
  border: none;
  display: inline-flex;
  z-index: 1;
  padding: 0 5px;
  margin: 10px;
  background-color: transparent;
  text-decoration: none;
  align-items: center;
  cursor: pointer;
  margin-right: 0;
}
.prev:hover > img[aria-readonly=false] {
  filter: invert(31%) sepia(97%) saturate(3775%) hue-rotate(215deg) brightness(101%) contrast(83%);
}
.prev > img[aria-readonly=true] {
  filter: invert(87%) sepia(10%) saturate(149%) hue-rotate(166deg) brightness(93%) contrast(87%);
}

.next {
  border: none;
  display: inline-flex;
  z-index: 1;
  padding: 0 5px;
  margin: 10px;
  background-color: transparent;
  text-decoration: none;
  align-items: center;
  cursor: pointer;
  margin-right: 0;
}
.next:hover > img[aria-readonly=false] {
  filter: invert(31%) sepia(97%) saturate(3775%) hue-rotate(215deg) brightness(101%) contrast(83%);
}
.next > img[aria-readonly=true] {
  filter: invert(87%) sepia(10%) saturate(149%) hue-rotate(166deg) brightness(93%) contrast(87%);
}

.indicator {
  background-color: #C2C8CD;
  border-radius: 14px;
  width: 10px;
  height: 10px;
  display: inline-flex;
  margin: 15px 1px 0;
}
.indicator-active {
  background-color: #2A6CEA;
}

@media screen and (max-width: 900px) {
  .content {
    width: 100%;
    margin: 0;
  }
  .prev {
    margin-left: 0%;
  }
  .next {
    margin-right: 0%;
  }
}/*# sourceMappingURL=Carousel.module.css.map */