
.nav {
  display: flex;
  position: absolute;
  top: 0;
  width: 100vw;
  padding: 20px 0;
  color: white;
  vertical-align: middle;

  &_btn {
    position: absolute;
    z-index: -1;
    padding: 30px;
    top: 0;
    right: 5px;
    margin-top: -100%;
  }
}

.name {
  display: flex;
  margin-left: 20%;
  width: 20%;
  align-self: center;
}

.logo {
  display: inline-flex;
  margin-right: 10px;
}

.links {
  display: flex;
  width: 40%;
  max-width: 470px;
  margin: 0 auto;
  text-align: center;
  
  a {
    text-decoration: none;
    color: inherit;
    margin: 0 5px;
    
    &:hover {
      text-decoration: underline;
    }
  }
  ul {
    width: 100%;
    display: flex;
    justify-content: space-between;
    list-style-type: none;
    margin: 0;
    padding: 0;
    align-items: center;

    &>li {
      display: inline-flex;

      &>a {
        &>img {
          position: absolute;
          margin-top: 5px;
          right: 30px;
          z-index: -1;
        }
      }
    }
  }
}



@media screen and (max-width: 900px) {

  .name {
    position: absolute;
    top: 35px;
    left: 30px;
    margin-left: 0;
  }

  .logo {
    margin-right: 50%;
  }

  .links {
    z-index: -1;
  }

  .nav_btn {
    z-index: 3;
    position: absolute;
    top: 13px;
    margin-top: 0;
  }

  .links {

    &>ul {

      &>li {
        position: absolute;
        text-align: left;

        &>a {
          width: 100%;
        }
      }
    }
  }

  .nav[aria-disabled="false"] {
    position: fixed;
    height: 100%;
    width: 100%;
    background-color: white;
    z-index: 2;
    overflow: hidden;

    &+.nav_btn {
      position: fixed;
      margin-top: 0;
    }

    .name{
      position: absolute;
      margin: 0;
      left: 16px;
      top: 28px;
      color: #000;
    }

    .links{
      display: flex;
      width: 100%;
      color: #83898F;
      font-size: 18px;
      line-height: 32px;
      margin-left: auto;
      margin-right: auto;
      margin-top: 80px;

      &>ul {
        width: 100%;
        display: flex;
        flex-direction: column;
        margin-right: auto;
        margin-left: auto;
        flex-wrap: wrap;
        justify-content: flex-start;
        align-content: center;
        
        &>li{
          position: relative;
          padding: 20px;
          width: 95vw;
          margin-left: auto;
          margin-right: auto;
          border-top: 1px #ECEFF2 solid;
          border-bottom: 1px #ECEFF2 solid;
        }
      }
    }
  }
}

@media screen and (max-width: 500px) {
  .logo {
    margin-right:15px;
  }
}