.accordion {
  display: flex;
  flex-direction: column;
  width: 100%;
  border-top: 1px solid #ECEFF2;
  padding: 20px 0;
}


.head {
  display: flex;
  align-items: center;
  cursor: pointer;

  &>img {
    margin-left: auto;
    transition: all .1s linear;

    &.open {
      transform: rotate(-45deg);
    }
    
    &.close {
      transform: rotate(0);
    }  
  }

  &:hover {
    filter: invert(31%) sepia(97%) saturate(3775%) hue-rotate(215deg) brightness(101%) contrast(83%);

    &>img {
      filter: invert(31%) sepia(97%) saturate(3775%) hue-rotate(215deg) brightness(101%) contrast(83%);
    }
  }
}



.text {
  padding: 20px 0 0;
  color: #5E646A;
}

@media screen and (max-width: 500px) {
  .text {
    font-size: 16px;
    line-height: 28px;
  }
}
