.main {
  padding: 20px 40px;
  border-radius: 4px;
  border: none;
  background-color: #ECEFF2;
  cursor: pointer;
}
.main:hover {
  padding: 20px 40px;
  border-radius: 4px;
  border: none;
  background-color: white;
  cursor: pointer;
  outline-offset: -1px;
  outline: 1px solid #C2C8CD;
}

.form {
  padding: 20px 40px;
  border-radius: 4px;
  border: none;
  background-color: #2A6CEA;
  cursor: pointer;
  color: white;
  box-shadow: 0 4px 4px rgba(0, 0, 0, 0.1);
}
.form:hover {
  padding: 20px 40px;
  border-radius: 4px;
  border: none;
  background-color: #397DFF;
  cursor: pointer;
  box-shadow: none;
}/*# sourceMappingURL=Button.module.css.map */